import React, { useState } from 'react';
import { Search, Plus, Trash2, Check, X, BookOpen } from 'lucide-react';

interface Course {
  id: string;
  code: string;
  name: string;
  credits: number;
  lecturer: string;
  schedule: string;
  room: string;
  capacity: number;
  enrolled: number;
}

const mockCourses: Course[] = [
  {
    id: '1',
    code: 'CS101',
    name: 'Introduction to Programming',
    credits: 3,
    lecturer: 'Dr. Ahmad Rahman',
    schedule: 'Mon 09:00-11:00',
    room: 'A201',
    capacity: 40,
    enrolled: 35,
  },
  {
    id: '2',
    code: 'CS201',
    name: 'Data Structures',
    credits: 3,
    lecturer: 'Prof. Sarah Kim',
    schedule: 'Wed 13:00-15:00',
    room: 'B305',
    capacity: 35,
    enrolled: 30,
  },
  {
    id: '3',
    code: 'CS301',
    name: 'Database Systems',
    credits: 3,
    lecturer: 'Dr. Robert Chen',
    schedule: 'Fri 10:00-12:00',
    room: 'C102',
    capacity: 30,
    enrolled: 25,
  },
];

const KRSManagement: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCourses, setSelectedCourses] = useState<Course[]>([]);
  const [availableCourses] = useState<Course[]>(mockCourses);

  const filteredCourses = availableCourses.filter(course =>
    course.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    course.code.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const addCourse = (course: Course) => {
    if (!selectedCourses.find(c => c.id === course.id)) {
      setSelectedCourses([...selectedCourses, course]);
    }
  };

  const removeCourse = (courseId: string) => {
    setSelectedCourses(selectedCourses.filter(c => c.id !== courseId));
  };

  const totalCredits = selectedCourses.reduce((sum, course) => sum + course.credits, 0);

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">KRS Management</h1>
        <div className="text-sm text-gray-500">Semester: Ganjil 2024/2025</div>
      </div>

      {/* Summary Card */}
      <div className="card">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <div className="text-center">
            <div className="text-2xl font-bold text-primary-600">{selectedCourses.length}</div>
            <div className="text-sm text-gray-500">Selected Courses</div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-green-600">{totalCredits}</div>
            <div className="text-sm text-gray-500">Total Credits</div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-blue-600">24</div>
            <div className="text-sm text-gray-500">Max Credits</div>
          </div>
          <div className="text-center">
            <div className={`text-2xl font-bold ${totalCredits <= 24 ? 'text-green-600' : 'text-red-600'}`}>
              {24 - totalCredits}
            </div>
            <div className="text-sm text-gray-500">Remaining</div>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Available Courses */}
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-gray-900">Available Courses</h2>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <input
                type="text"
                placeholder="Search courses..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 input-field"
              />
            </div>
          </div>

          <div className="space-y-3 max-h-96 overflow-y-auto">
            {filteredCourses.map((course) => (
              <div key={course.id} className="p-4 border border-gray-200 rounded-lg hover:bg-gray-50">
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center space-x-2">
                      <span className="font-medium text-gray-900">{course.code}</span>
                      <span className="text-sm text-gray-500">({course.credits} SKS)</span>
                    </div>
                    <div className="text-sm text-gray-900 mt-1">{course.name}</div>
                    <div className="text-xs text-gray-500 mt-1">
                      {course.lecturer} • {course.schedule} • {course.room}
                    </div>
                    <div className="text-xs text-gray-500">
                      Capacity: {course.enrolled}/{course.capacity}
                    </div>
                  </div>
                  <button
                    onClick={() => addCourse(course)}
                    disabled={!!selectedCourses.find(c => c.id === course.id) || course.enrolled >= course.capacity}
                    className="p-2 text-primary-600 hover:text-primary-700 hover:bg-primary-50 rounded-lg disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    <Plus className="h-4 w-4" />
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Selected Courses */}
        <div className="card">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-gray-900">Selected Courses</h2>
            <div className="flex space-x-2">
              <button className="btn-secondary text-sm">
                <X className="h-4 w-4 mr-1" />
                Clear All
              </button>
              <button 
                disabled={selectedCourses.length === 0}
                className="btn-primary text-sm disabled:opacity-50"
              >
                <Check className="h-4 w-4 mr-1" />
                Submit KRS
              </button>
            </div>
          </div>

          {selectedCourses.length === 0 ? (
            <div className="text-center py-12 text-gray-500">
              <BookOpen className="h-12 w-12 mx-auto mb-4 text-gray-300" />
              <p>No courses selected yet</p>
              <p className="text-sm">Add courses from the available list</p>
            </div>
          ) : (
            <div className="space-y-3 max-h-96 overflow-y-auto">
              {selectedCourses.map((course) => (
                <div key={course.id} className="p-4 border border-gray-200 rounded-lg bg-primary-50">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <div className="flex items-center space-x-2">
                        <span className="font-medium text-gray-900">{course.code}</span>
                        <span className="text-sm text-gray-500">({course.credits} SKS)</span>
                      </div>
                      <div className="text-sm text-gray-900 mt-1">{course.name}</div>
                      <div className="text-xs text-gray-500 mt-1">
                        {course.lecturer} • {course.schedule} • {course.room}
                      </div>
                    </div>
                    <button
                      onClick={() => removeCourse(course.id)}
                      className="p-2 text-red-600 hover:text-red-700 hover:bg-red-50 rounded-lg"
                    >
                      <Trash2 className="h-4 w-4" />
                    </button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default KRSManagement;